.dashboard-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
    background-color: #f5f7fa;
    min-height: calc(100vh - 140px); /* Account for header and footer */
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}

.header h1 {
    color: #2d3748;
    margin: 0;
    font-size: 24px;
}

.date {
    color: #718096;
    font-size: 14px;
}

.dashboard {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: auto;
    gap: 20px;
}

@media (max-width: 768px) {
    .dashboard {
        grid-template-columns: 1fr;
    }
}

.card {
    background-color: white;
    border-radius: 12px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    padding: 20px;
    transition: all 0.3s ease;
}

.card:hover {
    box-shadow: 0 10px 15px rgba(0, 0, 0, 0.1);
    transform: translateY(-2px);
}

.card-title {
    font-size: 18px;
    font-weight: 600;
    color: #4a5568;
    margin-top: 0;
    margin-bottom: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.info-icon {
    cursor: pointer;
    color: #a0aec0;
}

.energy-gauge-container {
    grid-column: 1;
    grid-row: 1;
}

.status-summary-container {
    grid-column: 2;
    grid-row: 1;
}

.forecast-chart {
    height: 200px;
    width: 100%;
}

.quick-actions {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
}

.action-button {
    flex: 1 1 calc(50% - 10px);
    min-width: 120px;
    padding: 15px;
    border: none;
    border-radius: 8px;
    background-color: #4299e1;
    color: white;
    font-weight: 600;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.2s ease;
}

.action-button:hover {
    background-color: #3182ce;
}

.action-button.meal {
    background-color: #38b2ac;
}

.action-button.meal:hover {
    background-color: #319795;
}

.loading-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: calc(100vh - 140px);
}

.loading-spinner {
    border: 4px solid rgba(0, 0, 0, 0.1);
    border-radius: 50%;
    border-top: 4px solid #4299e1;
    width: 40px;
    height: 40px;
    animation: spin 1s linear infinite;
    margin-bottom: 20px;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}