.sign-in-page {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: calc(100vh - 140px); /* Account for header and footer */
  padding: 20px;
  background-color: #f5f7fa;
}

.sign-in-container {
  max-width: 600px;
  width: 100%;
  background-color: white;
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  padding: 32px;
}

.welcome-section {
  text-align: center;
  margin-bottom: 24px;
}

.welcome-section h1 {
  color: #2d3748;
  font-size: 24px;
  margin-bottom: 12px;
}

.welcome-section p {
  color: #718096;
  margin-bottom: 24px;
}

.form-section {
  margin-bottom: 32px;
}

.form-section h2 {
  font-size: 18px;
  margin-bottom: 24px;
  color: #4a5568;
}

.form-group {
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  margin-bottom: 8px;
  font-weight: 500;
  color: #4a5568;
}

.form-group input {
  width: 100%;
  padding: 12px 16px;
  border: 1px solid #e2e8f0;
  border-radius: 8px;
  font-size: 16px;
  transition: border-color 0.2s;
}

.form-group input:focus {
  border-color: #4299e1;
  outline: none;
  box-shadow: 0 0 0 3px rgba(66, 153, 225, 0.15);
}

.form-link {
  text-align: right;
  margin-top: 8px;
}

.form-link a {
  color: #4299e1;
  text-decoration: none;
  font-size: 14px;
}

.form-link a:hover {
  text-decoration: underline;
}

.error-message {
  background-color: #fed7d7;
  color: #c53030;
  padding: 12px 16px;
  border-radius: 8px;
  margin-bottom: 20px;
  font-size: 14px;
}

.navigation-buttons {
  display: flex;
  justify-content: space-between;
  gap: 16px;
}

.btn {
  flex: 1;
  padding: 12px 24px;
  font-size: 16px;
  font-weight: 600;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s;
  display: flex;
  justify-content: center;
  align-items: center;
}

.btn-outline {
  background-color: transparent;
  border: 1px solid #4299e1;
  color: #4299e1;
}

.btn-outline:hover {
  background-color: #ebf8ff;
}

.btn-primary {
  background-color: #4299e1;
  border: 1px solid #4299e1;
  color: white;
}

.btn-primary:hover {
  background-color: #3182ce;
  border-color: #3182ce;
}

.btn:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

@media (max-width: 640px) {
  .sign-in-container {
    padding: 24px;
  }
  
  .navigation-buttons {
    flex-direction: column;
  }
  
  .welcome-section h1 {
    font-size: 20px;
  }
}